import React from 'react';
import s from './Dialogs.module.scss'
import DialogItem from './DialogItem/DialogItem';
import MessageItem from './MessageItem/MessageItem';
import { Field, reduxForm } from "redux-form";
import { required, maxLengthCreator } from '../../utilits/validators/validators';
import { Textarea } from '../../common/FormsControls/FormsControls';

const Dialogs = (props) => {
	let State = props.DialogsPage
	let DialogItems = State.DialogsData
		.map((d, ind) => {
			return <DialogItem key={d.id} name={d.name} id={d.id} />
		})

	let MessagesItem = State.MessagesData
		.map((m, ind) => {
			return <MessageItem key={m.id} message={m.message} id={m.id} />
		})

	let addNewMessage = (values) => {
		props.addMessage(values.newMessageBody)
	}


	return <div>
		<div className={s.dialogs}>
			<div className={s.dialogsItems + " " + s.items} >
				{DialogItems}
			</div>
			<div className={s.messages + " " + s.items}>
				{MessagesItem}
			</div>
		</div >
		<AddMessageFormRedux onSubmit={addNewMessage} />
	</div >
}


const addMessageForm = (props) => {
	return (
		<form onSubmit={props.handleSubmit}>
			<div>
				<Field validate={[required, maxLengthCreator(20)]} component={Textarea} name="newMessageBody" placeholder="Enter your message" />
				<div><button className={s.button} >add message</button></div>
			</div>
		</form>
	)
}

const AddMessageFormRedux = reduxForm({ form: "dialogAddMessageForm" })(addMessageForm)



export default Dialogs;